@tailwind base;
@tailwind components;
@tailwind utilities;
*{
  padding: 0;
  margin: 0;
  font-family: 'Vollkorn', serif;

}
body{
  background-color:  #100f0f;
}
.Name{
  background-clip: text;
  color: transparent;
  background-image: linear-gradient(to right, #00f5e1, rgb(0, 255, 0));
}
.bor:hover{
  box-shadow: 0 0 10px 3px #09ec82;
  background-color: #09ec82;
  transition: all 500ms ease;
}
.li{
  box-shadow: inset -2px 1px 11px rgb(0, 255, 255);
}
.li:hover{
  background-color: #00ffff ;
  transition: all 500ms ease;
  color: black;
}
.si {
 height: 10px;
 width: 40px;
right: 40px;
visibility: hidden;
opacity: 0;
transition: all 500ms ease;
}
.si.open {
  visibility: visible;
  opacity: 1;
right: 10px;
 width: 150px;
 height: 70vh;
}
.si > li{
  transition: all 500ms ease;
}

.circle{
  border-radius: 85% 15% 80% 20% / 16% 73% 27% 84% ;
}
@keyframes slideUpDown {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(30px);
  }
}
.hover-zoom {
  transition: transform 0.3s ease-in-out;
}

.hover-zoom:hover {
  transform: scale(1.1);
}

.animate-image {
  animation: slideUpDown 4s infinite;
}



.shadow{
  box-shadow: 0 0 30px 10px #05d5ff;
}
.box{
  border-radius: 10px 10px 0 0;
}
.val{
  color:#f702ef88
}
.behind{
  box-shadow: 0 0 10px 3px #d505ff;
  filter: blur(10px); 
  border-radius: 97% 3% 0% 100% / 55% 100% 0% 45% ;
  background: linear-gradient(to right, #8009007a, #fffb007a);  
}
.behindevr{
  box-shadow: 0 0 10px 3px #d505ff44;
  filter: blur(10px); 
  background: linear-gradient(to right, #8000557a, #00ff807a);  
  border-radius:45% 55% 100% 0% / 100% 49% 51% 0% ;
}
.behindtop{
  border-radius: 43% 57% 57% 43% / 0% 100% 0% 100% ;
  box-shadow: 0 0 10px 3px #05ff2f44;
  filter: blur(10px);  
  background: linear-gradient(to top, #8000067a, #bbff007a);  

}
.btn, .btns {
  border-radius: 10px;
  padding: 2px 8px;
  border: 2px solid #298540;
  transition: background 500ms ease-in-out;
}

.btn:hover{
  background: linear-gradient(to right, #8fff05fd, #ff057e);
}
.btn{
  background: linear-gradient(to right, #ff0505fd, #ffbc05);

}
.btns{
  background: linear-gradient(to right, #8605fffd, #93ff05);

}
.btns:hover {
  background: linear-gradient(to right, #ff057e, #2f05ff);
}
.btn-animation{
  animation: slideIn 1s ease-out;
}
@keyframes slideIn {
  from {
    transform: translateY(50px); 
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.opencir{
  border-radius: 0% 100% 100% 0% / 0% 50% 50% 100% ;
  box-shadow: 0 0 10px 3px #05ff2f44;
  filter: blur(10px);  
  background: linear-gradient(to top, #0080067a, #bbff007a);
}
.head::first-letter{
  color: red;
  font-size: 40px;
}
.head{
  font-size: 20px;
  font-style: oblique;
  text-align: center;
}
.value>li::before {
  content: "\1F525"; 
  margin-right: 5px;
  color: red;
  display: inline-block;
  animation: flame 1s infinite alternate-reverse;
}
.data>li::before {
  content: "\1F4A5"; 
  margin-right: 5px;
  color: red;
  display: inline-block;
  animation: splashAnimation 1s infinite alternate-reverse;
}
@keyframes splashAnimation {
  0% {
    transform: scaleZ(0.4);
    opacity: 0;
  }
  100% {
    transform: scaleZ(1);
    opacity: 1;
  }
}

@keyframes flame {
  0% {
    transform: scaleY(1);
  }
  50% {
    transform: scaleY(0.8);
  }
  100% {
    transform: scaleY(1);
  }
}

.paragraph::first-letter{
  padding-left: 30px;
  font-size: 20px;
  font-style: italic;
color: #ff8800;
}
.openbr{
  border-radius: 0% 100% 0% 100% / 0% 98% 2% 100% ;
  box-shadow: 0 0 10px 3px #05ffff44;
  filter: blur(10px);  
  background: linear-gradient(to top, #0071807a, #ff00dd7a);
}
.behindesr{
  border-radius: 100% 0% 0% 100% / 0% 0% 100% 100% ;
  box-shadow: 0 0 10px 3px #ff050544;
  filter: blur(10px);  
  background: linear-gradient(to top, #8000007a, #15ff007a);
}


.book {
  position: relative;
  border-radius: 10px;
  width: 200px;
  height: 230px;
  -webkit-box-shadow: 1px 1px 12px #000;
  box-shadow: 1px 1px 12px #000000;
  -webkit-transform: preserve-3d;
  -ms-transform: preserve-3d;
  transform: preserve-3d;
  -webkit-perspective: 2000px;
  perspective: 2000px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;

}

.cover {
  top: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  cursor: pointer;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform-origin: 0;
  -ms-transform-origin: 0;
  transform-origin: 0;
  -webkit-box-shadow: 1px 1px 12px #000;
  box-shadow: 1px 1px 12px #000;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}

.book:hover .cover {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: rotatey(-80deg);
  -ms-transform: rotatey(-80deg);
  transform: rotatey(-80deg);
}





.div {
  width: 105px;
  height: 35px;
  box-shadow: 16px 14px 20px #0000008c;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.div::before{
  content: "";
  background-image: conic-gradient(
      #ff0052 20deg,
      transparent 120deg
  );
  width: 150%;
  height: 150%;
  position: absolute;
  animation: rotate 1s linear infinite;
}
.div::after{
  content: "View More";
  width: 100px;
  height: 30px;
  background: #101010;
  position: absolute;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ff0052;
  box-shadow: inset 20px 20px 20px #0000008c;
}
@keyframes rotate {
  0%{
      transform: rotate(0deg);
  }
  100%{
      transform: rotate(-360deg);
  }
} 
.div::after:hover {
  background: #ff0052;
  color: #000;
}
