.hire {
    position: relative;
    text-align: center;
    color: black;
    background: transparent;
    cursor: pointer;
    background-color: rgba(157, 255, 0, 0.747);
    transition: ease-out 0.5s;
    border: 2px solid #725AC1;
    border-radius: 10px;
    box-shadow: inset 0 0 0 0 #725AC1;
  }
  
  .hire:hover {
    color: white;
    box-shadow: inset 0 -100px 0 0 #725AC1;
  }
  
  .hire:active {
    transform: scale(0.9);
  }