.inp,
.inpt {
  border-bottom: 2px solid black;
  background: transparent;
  outline: none;
  transition: all 1s ease-in-out;
  color:white
}

.inpt {
  resize: none;
}
.textarea >.inpn{
  bottom: 10px;
  transition: all 0.5s ease-in-out;
}

.inp:focus,
.inpt:focus,
.inp:hover,
.inpt:hover {
  border-bottom: 2px solid rgb(255, 0, 200);
}
.inp:focus ~ .inpn,
.inp:hover ~ .inpn {
    top: -50%;
    font-size: 13px;
    color: rgb(255, 0, 200);
}
.inpt:hover~ .inpn,
.inpt:focus~ .inpn{
      top: -10%;
    font-size: 13px;
    color: rgb(255, 0, 200);
}
.inpn {
  position: absolute;
  left: 0;
  color: rgb(36, 187, 224);
  transition: all 0.5s ease-in-out;
}

.val .inpn,
.name .inpn {
  top: -50%;
  font-size: 13px;
  color: rgb(255, 0, 200);
}
.valText .inpn{
  top: -10%;
  font-size: 13px;
  color: rgb(255, 0, 200);
}
.button {
  padding: 2px 5px;
  font-size: 1rem;
  color: #fff;
  font-weight: 600;
  transition: all .2s;
  position: relative;
  border: none;
  background: #000;


}

.button:before,.button:after {
  content: "";
  position: absolute;
  top: 50%;
  background: #ff3d00;
  height: 108%;
  width: 0;
 
  z-index: -1;
  transition: all .7s;
  transform: translateY(-50%);
}

.button:before {
  left: 0%;
  

}

.button:after {
  right: 0%;


}

.button:hover {
  color: #fff;
  box-shadow: #f51a1a7f 0 30px 60px -12px inset, #ff00007f 0 18px 36px -18px inset;
  z-index: 2;
}

.button:hover::before {
  width: 50%;
  left: 50%;
}

.button:hover::after {
  width: 50%;
  right: 50%;
}